import React from "react";
import { motion } from "framer-motion";

import utilCss from "@/styles/utils.scss";
import Diagonal from "@/atoms/Diagonal";

import styles from "./index.scss";

export default () => {
  return (
    <>
      <div className={styles.heroClipper}>
        <motion.div
          variants={{
            initial: { scale: 1.33, rotate: 5, opacity: 0 },
            animate: { scale: 1, rotate: 0, opacity: 1 }
          }}
          transition={{ duration: 0.15, ease: "easeInOut" }}
          initial="initial"
          animate="animate"
          exit="initial"
          className={styles.hero}
        >
          <div className={styles.heroBox}>
            <h2>
              <strong>누구나 강연</strong>하고,
              <br />
              <span className={utilCss.noWrap}>
                <strong>누구나 학생</strong>이
              </span>
              <span className={utilCss.noWrap}>될 수 있다!</span>
            </h2>
            <hr />
            <h1>
              PROGRAMMING
              <br />
              GALLERY
              <br />
              CONFERENCE
              <br />
              <strong>2020</strong>
            </h1>
            <hr />
            <h3>
              프로그래밍 갤러리 컨퍼런스 <strong>2020</strong>
            </h3>
            <table>
              <tbody>
                <tr>
                  <th>장소</th>
                  <td>장소장소장소</td>
                </tr>
                <tr>
                  <th>일시</th>
                  <td>2019-02-15</td>
                </tr>
              </tbody>
            </table>
          </div>
        </motion.div>
      </div>
      <div className={styles.introduce}>
        <Diagonal className={styles.introduceDiagonal}></Diagonal>
        <div className={styles.introduceBox}>
          <h1>PGC란?</h1>
          <h2>“누구나 강연 할 수 있고 누구나 학생이 될 수 있다”. </h2>
          <p>
            디시인사이드의 개방성을 모토로 현직 개발자와 개발자 꿈나무 등 다양한
            계층으로 이루어진 프로그래밍 갤러리 이용자들간의 기술교류를 통해
            프로그래밍 갤러리의 활성화와 더불어 이용자들의 프로그래밍 능력 향상
            및 네트워크 기반을 활성합니다.
          </p>
        </div>
      </div>
    </>
  );
};
